.MovieList{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
}

.Item{
    margin-top: 10px ;
    height: 100px;
    width: 100px;
    border: solid black 1px;
    border-radius: 10px;
}

.Title{
    text-align: center;
    margin: 20px;
    color: #b96060;
}
.Button_Next_Prev{
    border: none;
    background-color: #b96060;
    color: white;
    margin: 20px;
    height: 30px;
    width: 50px;
    cursor: pointer;
}

.Button_Next_Prev:hover{
    background-color: #ffffff;
    color: #b96060;
}

.Title_Search{
    color: #b96060;
    text-align: center;
}

.Block_Search{
    display: flex;
    flex-wrap: wrap;
}
.Buttons_Search{
    text-align: center;
}