.List {
    width: 20%;
}

.Genres_list.darkMode {
    background-color: #b96060;
}

.Genres_list {
    margin-top: 30px;
    margin-left: 15px;
    padding: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgba(185, 96, 96, 0.3);
    width: 100px;
    margin-right: 20px;
    border-radius: 20px;
}



.Item {
    margin: 5px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    background-color: white;
}

.Item:hover {
    color: #b96060;
}